import React from "react";
import { useNavigate } from "react-router-dom";
import { ArrowLeft } from "../../assets";
import { Title, CartCard } from "../../components";

import { useWindowSize } from "../../hooks";
import { getCartBooks, useAppSelector } from "../../store";
import { Breakpoint, Color } from "../../ui";
import { ButtonArrow, CartContainer, CartWrapper, Message, StyledCartPage } from "./styles";

export const CartPage = () => {
  const navigate = useNavigate();
  const { width = 0 } = useWindowSize();
  const { cartBooks } = useAppSelector(getCartBooks);

  const handlePage = () => {
    navigate(-1);
  };

  return (
    <StyledCartPage>
      <CartWrapper>
        <ButtonArrow onClick={handlePage}>
          <ArrowLeft
            width={width < Breakpoint.MD ? "30" : "40"}
            fill={Color.Primary}
            stroke={Color.Primary}
          />
        </ButtonArrow>

        <Title value="Your cart" />

        <CartContainer>
          {cartBooks.length !== 0 ? (
            cartBooks.map((book) => {
              return <CartCard key={book.isbn13} book={book} />;
            })
          ) : (
            <Message>Your shopping cart is empty. </Message>
          )}
        </CartContainer>
      </CartWrapper>
    </StyledCartPage>
  );
};
